	font-family: Roboto, Helvetica, Arial, sans-serif;
	font-size: 14px;
	color: rgb(32, 32, 32);
	padding: 0;
	margin: 0;
}

table {
	font-size: 14px;
}

.pageLoading {
	display: flex;
	background: white;
	height: 100%;
	align-items: center;
	justify-content: center;
}

.noAccess {
	display: flex;
	background: white;
	height: 100%;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	font-size: 2rem;
	color: rgb(237 78 78);
}

.noaccess svg {
	width: 6rem;
	height: 6rem;
	fill: rgb(237 78 78);
}

h1 {
	text-align: center;
	font-family: 'Russo One';
	margin: 0px;
}

h4 {
	margin: 1vh 0;
}

svg {
	width: 24px;
	height: 24px;
}

.page {
	display: grid;
	grid-template-columns: auto 1fr; 
	min-height: 100vh;
	background-color: rgb(245 245 245);
}

header {
	display: flex;
	grid-column: 1 / -1;
	flex-direction: column;
}

header h1 {
	flex: 1;
	color: rgb(10 66 115);
}

header h1.subTitle {
	font-size: 1.5em;
	color: rgb(223 170 3);
}

.subHeading {
	font-size: .9em;
	color: rgb(121 121 121);
}

button, .button {
	cursor: pointer;
	user-select: none;
	outline: none;
}

button:disabled, .button:disabled {
	background-color: rgba(239, 239, 239, 0.3) !important;
	color: rgba(16, 16, 16, 0.3) !important;
	border-color: rgba(118, 118, 118, 0.3) !important;
	cursor: not-allowed;
}

.row {
	display: flex;
	align-items: center;
	column-gap: 1rem;
}
.row.expand {
	flex-wrap: wrap;
}

.row.spread {
	justify-content: space-between;
}

.error {
	flex: 1;
	color: rgb(217 53 53);
}

.menuIconToggle {
	position: fixed;
	margin: 10px;
	z-index: 10;
	background-color: rgb(245 245 245);
}

.container {
	transform: translateY(100vh);
	transition: transform 0.3s ease-in-out;
	display: grid;
	grid-column-gap: 2vw;
	grid-row-gap: 3vh;
	margin: 0 auto 18vh auto;
}
.container.active {
	transform: translateY(0);
	padding-top: 30px;
}

.panelHeader {
	font-size: 1.3em;
	font-family: 'Russo One';
	grid-column: 1 / -1;
	margin: auto;
	border-bottom: 3px solid rgb(87 87 87);
	color: rgb(87 87 87);
	width: 25rem;
}

.panelHeader div {
	background: rgb(245 245 245);
	width: max-content;
	margin: 0px auto -13px auto;
	padding: 0px 15px;
}

.panel {
	background-color: rgb(255 255 255);
	box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
	border-radius: 8px;
	padding: 10px 20px;
	width: 90vw;
	max-width: 450px;
	margin: 0 auto;
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	height: fit-content;
}

.panel.centered {
	grid-column: 1 / -1;
}

.panelContent {
	padding: 10px 20px;
	display: flex;
	flex-direction: column;
	height: fit-content;
	box-sizing: border-box;
}

.panel.actionBar {
	padding: 0px;
}

.panelActionBar {
	display: grid;
	grid-auto-flow: column;
}

.panel .panelActionBar .button, .panel .panelActionBar button {
	justify-self: center;
	font-size: .9em;
	display: flex;
	column-gap: .5rem;
	align-items: center;
	padding: 5px 0px;
	border-top: 2px solid rgb(227 227 227);
	border-right: 2px solid rgb(227 227 227);
	width: 100%;
	justify-content: center;
	color: rgb(111 111 111);
}

.panel .panelActionBar .button:last-child {
	border-right: none;
}

.panel h3 {
	margin: 10px 0;
	display: flex;
}

.panelLoading img {
	width: 50px;
	height: 50px;
}

.panelLoading {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100px;
}

.panelError {
	margin: 20px 0px;
	color: rgb(217 53 53);
	text-align: center;
}

.filterExpand {
	flex: 1;
	display: flex;
	justify-content: flex-end;
}

.filterContent {
	display: none;
}

.filterContent.active {
	display: grid;
}

.panel .icon {
	background-color: rgb(242 192 41);
	border: 3px solid rgb(191 126 4);
	padding: 6px 12px;
	border-radius: 30px;
	font-size: 1.5em;
}

label {
	display: block;
	margin: 20px 0;
	align-items: center;
}

label span {
	margin-right: 10px;
	font-weight: bold;
}

.panel textarea {
	width: 100%;
	height: 15vh;
	border: 0;
	border-top: 2px solid rgb(143 143 143);
	border-bottom: 2px solid rgb(143 143 143);
	font-family: Roboto, Helvetica, Arial, sans-serif;
	font-size: 1em;
	outline: none;
}

.panel input,
.panel select {
	border: 0;
	border-bottom: 2px solid rgb(143 143 143);
	background: none;
	font-size: 1em;
	font-family: Roboto, Helvetica, Arial, sans-serif;
	outline: none;
	width: 100%;
}

.panel button, .panel .button {
	font-family: Roboto, Helvetica, Arial, sans-serif;
	background-color: transparent;
	border: none;
	font-size: 1em;
}

.panel button.selected, .panel .pill .selected {
	border-color: rgb(0 128 0);
	background-color: rgb(109 215 109);
}

button svg, .button svg {
	width: 20px;
	height: 20px;
	fill: rgb(87 87 87);
}

.panel .rowContent {
	flex: 1;
}

.panel .action {
	border: none;
	background: none;
	fill: rgb(10 66 115);
}

.panel .action svg {
	width: 24px;
	height: 24px;
}

.action.notFavorite svg {
	fill: rgb(171 171 171);
}

.panel .seperator {
	border-top: 3px solid rgb(201 201 201);
	margin: 10px 0px;
}

.panel ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

.panel li:last-child {
	border: none;
}

.sectionList {
	display: flex;
	flex-wrap: wrap;
	gap: 10px;
	border-top: 2px solid rgb(199 199 199);
	border-bottom: 2px solid rgb(199 199 199);
	padding: .7rem 0px;
	margin: 1rem 0px;
	overflow-y: auto;
}

td.sectionList.selected {
	border-color: rgb(81 179 81);
	border-width: 3px;
}

.sectionHeading {
	color: rgb(87 87 87);
	font-size: 14px;
	margin-top: 10px;
}

.inlay {
	border: 1px solid rgb(145 145 145);
	border-radius: 4px;
	box-shadow: inset rgba(50, 50, 93, 0.25) 0px 0px 14px -3px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
	overflow: auto;
	background-color: rgb(245 246 250);
}

.pill {
	font-size: .85rem;
	background-color: rgb(239 239 239);
	border: 2px solid rgb(151 151 151);
	border-radius: 5px;
	padding: 1px 6px;
	display: flex;
	align-items: center;
	color: rgb(89 89 89);
}

.pill button svg {
	width: 14px;
	height: 14px;
	fill: rgb(89 89 89);
}

.tableContainer {
	max-height: 200px;
	overflow-y: auto;
	margin: 5px 0px 20px 0px;
	box-shadow: inset rgb(205 205 205) 0px 0px 4px 0px;
	padding: 2px 7px;
	border-radius: 9px;
	display: flex;
}

table.sectionTable {
	font-size: .85rem;
	flex: 1;
	border-collapse: collapse;
	margin: 10px 0px;
	border-top: 3px solid rgb(199 199 199);
	border-bottom: 3px solid rgb(199 199 199);
}

.sectionTable th {
	text-align: left;
	padding: 7px 0px 3px 0px;
	font-size: 1.1em;
}

.sectionTable .dataColumn {
	text-align: center;
}

.sectionTable tr {
	border-bottom: 2px dashed rgb(243 243 243);
}

.sectionTable tbody tr:last-child {
	border-bottom: none;
}

.sectionTable td {
	padding: 4px 3px;
}

.sectionTable td:nth-child(1) {
	padding-left: 0;
}

.sectionTable td:last-child {
	padding-right: 0;
}

.sectionTable td.emptyTable {
	text-align: center;
	padding: 20px 0px;
}

.sectionTable td:last-child {
	padding-right: 0;
}

.sectionTable .predata {
	white-space: pre-line;
}

.textList {
	margin: 15px 10px;
	padding-bottom: 5px;
	font-size: .85em;
	color: rgb(80 80 80);
	display: grid;
	grid-auto-flow: column;
	grid-auto-columns: max-content;
	grid-gap: .5rem;
	overflow-x: scroll;
}

.dataGrid {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
	font-size: .85rem;
	grid-gap: 1rem;
	color: rgb(87 87 87);
}

.bottomNav {
	position: fixed;
	left: 0;
	right: 0;
	bottom: 0;
	height: 60px;
	background: rgb(255 255 255);
	display: grid;
	box-shadow: rgba(0, 0, 0, 0.15) 5px -2px 8px 0px;
	align-items: center;
	grid-auto-flow: column;
}

.bottomNav button, .bottomNav .button {
	border: none;
	background-color: transparent;
	display: grid;
	grid-auto-flow: row;
	font-size: .9em;
	justify-content: center;
	color: rgb(79 79 79);
	fill: rgb(79 79 79);
}

.bottomNav svg {
	width: 24px;
	height: 24px;
	margin: auto;
}

.refreshDisplay.refresh {
	color: rgb(229 87 87);
}

.listItem {
	border-top: 2px solid rgb(199 199 199);
	border-right: 2px solid rgb(199 199 199);
	border-left: 7px solid rgb(203 203 203);
	padding: 5px 5px;
	background-color: rgb(255 255 255);
}

.listItem:nth-child(2) {
	border-left: 7px solid rgb(109 215 109);
}

.listItem:nth-child(3) {
	border-left: 7px solid rgb(247 243 113);
}

.listItem:last-child {
	border-bottom: 2px solid rgb(199 199 199);
}

.listItem.dragging {
	position: relative;
	box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgb(0 0 0 / 18%) 0px 1px 6px 1px;
}

.listItemHeader {
	color: rgb(87 87 87);
	font-size: 1.2em;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.selectorContainer {
	margin: 1rem 0;
	display: grid;
	grid-template-columns: 1fr 1fr;
	background-color: rgb(99 99 99);
	border-radius: 4px;
	padding: 3px;
	cursor: pointer;
	user-select: none;
	outline: none;
}

.selector {
	padding: .25rem 0;
	text-align: center;
	color: rgb(255 255 255);
}

.selector.selected {
	border-radius: 2px;
	background-color: rgb(161 161 161);
	cursor: initial;
}

.fullSize {
	display: none;
}

@media (min-width: 1024px) {
	.container {
		grid-template-columns: repeat(auto-fit, minmax(410px, 1fr));
		max-width: 60vw;
	}

	.menuIconToggle {
		display: none;
	}

	.bottomNav {
		left: 20rem;
	}

	.panel {
		width: 400px;
	}

	.panel.filter {
		grid-column: 1 / -1;
		max-width: inherit;
		width: 71vw;
		display: grid;
		grid-template-columns: minmax(min-content, 90px) 1fr;
	}

	.panel.expandable {
		max-width: inherit;
		width: 71vw;
		grid-column: 1 / -1;
	}

	.filterExpand {
		display: none;
	}

	.filterContent {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-column-gap: 1rem;
	}

	.filterContent label {
		display: grid;
		margin: unset;
		grid-template-columns: min-content 1fr;
		grid-column-gap: 1rem;
	}

	.fullSize {
		display: block;
	}

}
